import { Helmet } from 'react-helmet'
import StartLogo from '../../../components/Logo'
import LinkToAndr7stIndex from '../../../components/Andr7stIndex'
import internetLinksTo from '../assets/data'

// ES6 feature:
const redirectToAnotherSite = (siteLink, delaySecons) => {
  let timeSeconds = delaySecons;
  let timeTimerId = setInterval(function () {
    timeSeconds--;    
    if(timeSeconds === 0) {
      window.location.href = siteLink;
      clearInterval(timeTimerId);
    }
  }, 1000);
}


const RedirPage = ({parametroUrl}) => {

  let isDefaultRedir = false
  let myLinkId = "000"

  let redirectTimeOut = 2
  let gisCode = internetLinksTo.find(gisCode => gisCode.globalId === parametroUrl )
  let fullPathToRedirect = 'https://gist.github.com/Andr7st/' // debe terminar con una barra inclinada.

  console.log(parametroUrl)
  
  // En el caso de que el parametro sea correcto y este en el json
  if(gisCode?.globalId) {
    myLinkId = gisCode.linkId
    fullPathToRedirect += myLinkId
    redirectToAnotherSite(gisCode.comentary, redirectTimeOut)
  } else {
    // Si redirecciona pero no al sitio por defecto
    isDefaultRedir=true
    redirectToAnotherSite(fullPathToRedirect, 3)

  }


  return (
    <div className="style-homePage">
      <Helmet> <title> Andr7st | Redirect </title></Helmet>

        <br/>

        <p> | Andr7st | {gisCode?.globalId} </p>

        {gisCode?.globalId && <div>
              <p>redirecting in a moment to:</p>
              <p> <a href={fullPathToRedirect}> {fullPathToRedirect} </a></p>
          </div>
        }

        { isDefaultRedir && 
          <div>
            <p><a href={fullPathToRedirect}> {fullPathToRedirect} </a></p>
          </div>
        }
          
        <div className="logo-start" >
          <StartLogo />
        </div>
        <br/>
        <LinkToAndr7stIndex />
  
      </div>
    )
  }
  
export default RedirPage
  